import React, { Component } from 'react';
import './Speaker.css';

class Speaker extends Component {
  constructor(props) {
    super();
    this.state = {
      uid: props.uid || null,
      name: props.name || "??",
      volume: props.volume || "??"
    };
    this.down = this.down.bind(this);
    this.up = this.up.bind(this);
    this.update = this.update.bind(this);
    this.timeout = null;
  }

  setVolume(newVolume) {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    let options = {
      method: 'POST',
      headers: headers,
      body: JSON.stringify({
        volume: newVolume
      })
    };
    fetch('http://192.168.2.76:8080/api/v1/speakers/' + this.state.uid,
          options)
      .then(response => response.json())
      .then(json => this.setState({ volume: json.volume }));
  }

  down() {
    this.setVolume(this.state.volume - 1);
  }

  up() {
    this.setVolume(this.state.volume + 1);
  }

  update() {
    if (this.props.match && this.state.uid) {
      fetch('http://192.168.2.76:8080/api/v1/speakers/' + this.state.uid)
        .then(response => response.json())
        .then(json => this.setState({ volume: json.volume }));
      this.updateLater();
    }
  }

  updateLater() {
    if (this.timeout !== null)
      clearTimeout(this.timeout);
    this.timeout = setTimeout(this.update, 10000);
  }

  componentDidMount() {
    if (this.props.match) {
      fetch('http://192.168.2.76:8080/api/v1/speakers')
        .then(response => response.json())
        .then(json => {
          for (let speaker of json.speakers) {
            if (speaker.name === this.props.match.params.speaker) {
              this.setState({
                uid: speaker.uid,
                name: speaker.name,
                volume: speaker.volume
              });
              this.updateLater();
              break;
            }
          }
        });
    }
  }

  componentWillReceiveProps(newProps) {
    this.setState({
      volume: newProps.volume
    });
  }

  render() {
    if (!this.state.uid)
      return <div />;

    return (
      <div className="Speaker"
           onClick={this.update}>
        <div className="Speaker-name">
          {this.state.name}
        </div>
        <div className="Speaker-volume">
          {this.state.volume}
        </div>
        <button className="Speaker-down"
                onClick={this.down}>-</button>
        <button className="Speaker-up"
                onClick={this.up}>+</button>
      </div>
    );
  }
}

export default Speaker;



